<template>
    <section class="section-dashboard min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <h3 class="title-vista text-2xl font-semibold text-neutral-600 pb-10">Monitorear abastecimientos</h3>
        <div class="overflow-x-auto shadow-md sm:rounded-lg p-5 bg-white">
            <DataTable :data="data" :headerList="headerList" class="overflow-x-scroll" />
        </div>
    </section>
</template>
<script>
import { defineComponent, ref, onMounted } from 'vue';
import { reportesData } from '../services/ReportsService';
import store from '@/store';
import DataTable from '@/components/reportes/DataTable.vue';

export default defineComponent({
    name: "MonitorearAbastecimientos",
    components: {
        DataTable,
    },
    setup() {
        const codempresa = store.state.codempresa;
        const data = ref([]);
        // Datos simulados para DataTable
        const headerList = ref([
            'Ticket',
            'Cisterna',
            'Nombre',
            'Cliente',
            'Detalle',
            'Conductor',
            'Equipo',
            'Facturación',
            'Fecha',
            'Galones',
            'Horometro',
            'Odometro',
            'Punto',
            'Recibido por',
            'Guia',
            'Ubicación',
        ]);
        onMounted(() => {
            actualizarDataMonitoreo();
            setInterval(actualizarDataMonitoreo, 15000);
        });
        const formarEstructuraFecha = (currentday) => {
            const year = currentday.getFullYear();
            const month = String(currentday.getMonth() + 1).padStart(2, '0');
            const day = String(currentday.getDate()).padStart(2, '0');
            return `${year}-${month}-${day}`;
        }

        const actualizarDataMonitoreo = () => {
            // Obtengo la fecha actual en formato 2023-05-25
            const currentDate = new Date();
            const Dateinicio = formarEstructuraFecha(currentDate);
            // Obtengo la fecha del dia siguiente en formato 2023-05-25
            const nextDay = new Date();
            nextDay.setDate(currentDate.getDate() + 1);
            const DateFin = formarEstructuraFecha(nextDay);
            reportesData("ALL", "ALL", Dateinicio, DateFin, codempresa)
                .then(response => {
                    data.value = response.data.data;
                })
        }
        return {
            data,
            headerList
        }
    }
})
</script>